import React from "react";
import Tree from "./index";
import i18n from "i18n/i18n";
import { FaCaretRight, FaPlusCircle,FaPencilAlt } from "react-icons/fa";
import { NavLink } from "reactstrap";


const TreeList = ({treeData, handleOnClickAddChild, handleOnClickEditItem, handleOnClickDeleteItem }) => {

  return (
    <>

      <div className="row">
        <div className="col">
        {Array.isArray(treeData) && <>
          <h3>{i18n.t("admin.major_standard_output_edit.MAJOR_STANDARD_OUTPUT")}<NavLink className="LinkButton" onClick={()=>handleOnClickAddChild({id: null, chuanDauRa: {mucDo:0, ten: ""}})}><FaPlusCircle /></NavLink></h3>
          <div className="mt-3">
            <div className="row mt-3 d-flex justify-content-left">
              <div className="col-lg-12 text-left text-dark">
                <Tree data={treeData} handleOnClickAddChild={handleOnClickAddChild} handleOnClickEditItem={handleOnClickEditItem} handleOnClickDeleteItem={handleOnClickDeleteItem} />
              </div>
            </div>
          </div>
        </>}
        </div>
      </div>
    </>
  );
};

export default TreeList;