import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { withFormik } from "formik";
import * as Yup from "yup";
import TreeList from "components/admin/Tree/TreeList";
import "./MajorStandardOutputEdit.css";
import { toast } from "react-toastify";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import PageHeader from "./components/PageHeader/PageHeader";
import standardOutputApi from "api/common/standardOutput";
import majorStandardOutputApi from "api/common/majorStandardOutput";
import subMajorApi from "api/common/subMajor";
import { useParams } from 'react-router-dom';
import {
  FaExchangeAlt,
  FaPlus,
  FaSync,
  FaTextWidth,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { RoutesString } from "pages/routesString";
import { useHistory } from "react-router-dom";

const initValues = {
  ma: "",
  ten: "",
  mucDo: "",
  chuanDauRa: "",
};

const MajorStandardOutputEdit = function ({
  values,
  handleChange,
  resetForm,
  errors,
}) {
  const { t } = useTranslation();
  const { id } = useParams();
  const history = useHistory();
  const [isUpdatePage, setIsUpdatePage] = useState(-1);
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [ currentOnClickItem, setCurrentOnClickItem ] = useState(null);
  const [ treeData, setTreeData ] = useState([]);
  const [ listStandardOutput, setListStandardOutput ] = useState([]);
  const [ submajor, setsubmajor ] = useState(null);
  const renderData = async () =>{
    setIsLoading(true);
      try{
        const res = await majorStandardOutputApi.getListChild(id);
        setTreeData([...res]);
      }
      
      catch(err)// eslint-disable-next-line
      {
        toast.error(t("admin.common.NOTTHINGTOSHOW"));
      }
      setIsLoading(false);
  }

  const renderHeader = async () => {
    const res = await subMajorApi.getById(id);
    if(res.id!==undefined) {
      setsubmajor(res);
    }else{
      toast.error(t("admin.common.NOTTHINGTOSHOW"));
      history.push(RoutesString.ADMIN_SUB_MAJOR_LIST);
    }
  }
  useEffect(() => {
    renderHeader();
    renderData();
  }, []);

  const resetNoti = ()=>{
    setNotification({
      color: "success",
      isOpen: false,
      messages: [],
    });
  }
  const handleEditOnClick = async function (e) {
    e.preventDefault();
    try {
      if (isUpdatePage === 1) {
        const res = await majorStandardOutputApi.putById(currentOnClickItem.id, {ma: values.ma, parent: currentOnClickItem.parent ? parseInt(currentOnClickItem.parent.id) : null, nganhDaoTao: parseInt(currentOnClickItem.nganhDaoTao.id), chuanDauRa: parseInt(values.chuanDauRa, 10)});
        if (res) {
          toast.success(t("UPDATE_SUCCESS"));
        }
      } else if(isUpdatePage === 0) {
        if(currentOnClickItem.id === null)
        {
          const res1 = await majorStandardOutputApi.post({ma: values.ma, parent:  null, nganhDaoTao: parseInt(id), chuanDauRa: parseInt(values.chuanDauRa, 10)});
          if (res1) {
            toast.success(t("ADD_SUCCESS"));
          }
        } else {
          const res1 = await majorStandardOutputApi.post({ma: values.ma, parent:  parseInt(currentOnClickItem.id), nganhDaoTao: parseInt(currentOnClickItem.nganhDaoTao.id), chuanDauRa: parseInt(values.chuanDauRa, 10)});
          if (res1) {
            toast.success(t("ADD_SUCCESS"));
          }
        }
      }
    } catch (err) {
      if (err.response) {
        if (err.response.status === 403 || err.response.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err.response.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err.response.status === 409) {
          toast.error(t("MAJOR_STANDARD_OUTPUT_EXIST"));
        } else if (err.response.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    }
    await renderData();
  };

  const handleRefreshOnClick = function (e) {
    e.preventDefault();
    resetForm();
    resetNoti();
  };

  const handleOnClickAddChild = async (node)=>{
    setIsLoading(true);
    setIsUpdatePage(0);
    setCurrentOnClickItem(node);
    values.mucDo = "";
    values.ten = "";
    values.ma = "";
    values.chuanDauRa = "";
    try{
      const res1 = await standardOutputApi.getAll({ mucDo: node.chuanDauRa.mucDo + 1 });
      if(Array.isArray(res1?.contents)) {
        setListStandardOutput(res1.contents)
      }
    }catch(err){
      setListStandardOutput([]);
    }
    setIsLoading(false);
  }

  const handleOnClickEditItem = async(node)=>{
    setIsLoading(true);
    setIsUpdatePage(1);
    setCurrentOnClickItem(node);
    initValues.mucDo = node.chuanDauRa.mucDo;
    initValues.ten = node.chuanDauRa.ten;
    initValues.ma = node.ma;
    initValues.chuanDauRa = node.chuanDauRa.id;
    try{
      const res2 = await standardOutputApi.getAll({ mucDo: node.chuanDauRa.mucDo });
      if(Array.isArray(res2?.contents)) {
        setListStandardOutput(res2.contents)
      }
    }catch(err){
      setListStandardOutput([]);
    }
    setIsLoading(false);
  }

  const handleOnClickDeleteItem = async (node) => {
    setIsLoading(true);
    try {
      const res = await majorStandardOutputApi.deleteById(node.id);
      if (res) {
        if (res.status === 200) {
          toast.success(t("DELETE_SUCCESS"));
        } else {
          toast.error(t("DELETE_ERROR"));
        }
      }
    } catch (err) {
      if (err.response) {
        if (err.response.status === 403 || err.response.status === 401) {
          toast.error(t("DELETE_ERROR")+": "+t("RESPONSE_MESSAGE401"));
        } else if (err.response.status === 400) {
          toast.error(t("DELETE_ERROR")+": "+t("RESPONSE_MESSAGE400"));
        } else if (err.response.status === 409) {
          toast.error(t("DELETE_ERROR")+": "+t("RESPONSE_MESSAGE409"));
        } else if (err.response.status === 404) {
          toast.error(t("DELETE_ERROR")+": "+t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("DELETE_ERROR")+": "+t("RESPONSE_MESSAGE500"));
        }
      }
    }
    setIsLoading(false);
    await renderData();
  }

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={t("admin.major_standard_output_edit.UPDATE")}
        items={[
          {
            href: RoutesString.ADMIN_SUB_MAJOR_LIST,
            title: t("admin.sub_major_list.LIST"),
          },
        ]}
      />

      <PageHeader
        headerTitle={`${t("admin.sub_major_list.STANDARD_OUTPUT")} of ${submajor===null?"":submajor.nganhDaoTao.ten+" - "+submajor.khoa}`}
      />
    <div className="row">
      <div className="page-content text-left col-lg-5">
        <TreeList treeData={[...treeData]} handleOnClickAddChild={handleOnClickAddChild} handleOnClickEditItem={handleOnClickEditItem} handleOnClickDeleteItem={handleOnClickDeleteItem}/>
      </div>
      {isUpdatePage !==-1 &&
      <div className="page-content col-lg-7">
        <h2>{
          isUpdatePage
            ? t("admin.major_standard_output_edit.EDIT_STANDARD_OUTPUT")
            : t("admin.major_standard_output_edit.ADD_STANDARD_OUTPUT")}
        </h2>
        <Form className="mt-4">
          <Notification
            color={notification.color}
            isOpen={notification.isOpen}
            messages={notification.messages}
          />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.major_standard_output_edit.NAME")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="ten"
                  placeholder={t("admin.major_standard_output_edit.NAME_PLACEHOLDER")}
                  value={values.ten}
                  onChange={handleChange}
                  invalid={errors.ten}
                  readOnly
                />
              </InputGroup>
              {errors.ten && <FormText color="danger">{errors.ten}</FormText>}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="value" className="col-lg-3">
              {t("admin.major_standard_output_edit.LEVEL")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaUserAlt />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="number"
                  name="mucDo"
                  placeholder={t(
                    "admin.major_standard_output_edit.LEVEL_PLACEHOLDER"
                  )}
                  value={values.mucDo}
                  onChange={handleChange}
                  invalid={errors.mucDo}
                  readOnly
                />
              </InputGroup>
              {errors.mucDo && (
                <FormText color="danger">{errors.mucDo}</FormText>
              )}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.major_standard_output_edit.CODE")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="ma"
                  placeholder={t("admin.major_standard_output_edit.CODE_PLACEHOLDER")}
                  value={values.ma}
                  onChange={handleChange}
                  invalid={errors.ma}
                />
              </InputGroup>
              {errors.ma && <FormText color="danger">{errors.ma}</FormText>}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.major_standard_output_edit.STANDARD_OUTPUT")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="select"
                  name="chuanDauRa"
                  value={values.chuanDauRa}
                  onChange={handleChange}
                >
                  <option key={1} value={0}>
                    {t("admin.major_standard_output_edit.STANDARD_OUTPUT_PLACEHOLDER")}
                  </option>
                  {listStandardOutput.length > 0 &&
                    listStandardOutput.map((item, index) => {
                      return (
                        <option key={index + 1} value={item.id}>
                          {item.ten} - {item.mucDo}
                        </option>
                      );
                    })}
                </Input>
              </InputGroup>
              {errors.chuanDauRa && (
                <FormText color="danger">{errors.chuanDauRa}</FormText>
              )}
            </div>
          </FormGroup>{" "}

          <br />
          <FormGroup>
            <Button color="success" onClick={handleEditOnClick}>
              {isUpdatePage ? (
                <>
                  <FaExchangeAlt /> {t("admin.common.UPDATE")}
                </>
              ) : (
                <>
                  <FaPlus /> {t("admin.common.ADD")}
                </>
              )}
            </Button>
            &emsp;
            {isUpdatePage===1 && (
              <>
                <Button onClick={handleRefreshOnClick}>
                  <FaSync /> {t("admin.common.REFRESH")}
                </Button>
              </>
            )}
          </FormGroup>
        </Form>
      </div>
      }
      </div>
    </div>
  );
};

const MajorStandardOutputEditFormikForm = withFormik({
  mapPropsToValues() {
    return initValues;
  },
  enableReinitialize: true,
  validationSchema: Yup.object().shape({
    mucDo: Yup.number()
      .required(i18n.t("REQUIRED_FIELD_WARNING"))
      .positive(i18n.t("admin.POSITIVE_WARNING")),
    ten: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
    ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
  }),
})(MajorStandardOutputEdit);

export default MajorStandardOutputEditFormikForm;
