import React, { useState } from "react";
import "./index.css";
import { FaCaretRight, FaPlusCircle,FaPencilAlt, FaTrashAlt } from "react-icons/fa";
import { useTranslation } from "react-i18next";
import {
  Button,
  NavLink
} from "reactstrap";
import ConfirmModal from "components/admin/ConfirmModal/ConfirmModal";
const Tree = ({ data = [], handleOnClickAddChild, handleOnClickEditItem, handleOnClickDeleteItem  }) => {
  return (
    <div className="d-tree">
      <ul className="d-flex d-tree-container flex-column">
        {data.map((tree) => (
          <TreeNode node={tree} handleOnClickAddChild={handleOnClickAddChild} handleOnClickEditItem={handleOnClickEditItem} handleOnClickDeleteItem={handleOnClickDeleteItem} />
        ))}
      </ul>
    </div>
  );
};

const TreeNode = ({ node, handleOnClickAddChild, handleOnClickEditItem, handleOnClickDeleteItem  }) => {
  const [childVisible, setChildVisiblity] = useState(false);
  const [confirmModal, setConfirmModal] = useState(false);
  const { t } = useTranslation();
  const confirmToggle = function () {
    setConfirmModal(!confirmModal);
  };

  const hasChild = node.chuanDauRa.mucDo<3 ? true : false;
  const handleCancelButtonOnClick = function (e) {
    e.preventDefault();
    confirmToggle();
  };
  return (
    <li className="d-tree-node border-0">

      <ConfirmModal
        modal={confirmModal}
        toggle={confirmToggle}
        headerTitle={t("admin.common.DELETE_CONFIRM_HEADER")}
        bodyContent={t("admin.common.DELETE_CONFIRM_BODY")}
        handleOkButtonOnClick={()=>{handleOnClickDeleteItem(node); confirmToggle();}}
        handleCancelButtonOnClick={handleCancelButtonOnClick}
      />
      <div className="d-flex">
        {hasChild && (
          <div
            className={`d-inline d-tree-toggler ${
              childVisible ? "active" : ""
            }`}
            onClick={(e) => setChildVisiblity((v) => !v)}
          >
            <FaCaretRight />
          </div>
        )}

        <div className="col d-tree-head">
         <span onClick={(e) => setChildVisiblity((v) => !v)}>{node.ma}/ {node.chuanDauRa.ten}</span><span><NavLink className="LinkButton" onClick={()=>handleOnClickEditItem(node)}><FaPencilAlt /></NavLink></span>
         {hasChild && <NavLink className="LinkButton" onClick={()=>handleOnClickAddChild(node)}><FaPlusCircle /></NavLink>}
          <span><NavLink className="LinkButton" onClick={()=>confirmToggle()}><FaTrashAlt /></NavLink></span>
        </div>
      </div>

      {hasChild && childVisible && (
        <div className="d-tree-content">
          <ul className="d-flex d-tree-container flex-column">
            <Tree data={node.child} handleOnClickAddChild={handleOnClickAddChild} handleOnClickEditItem={handleOnClickEditItem} handleOnClickDeleteItem={handleOnClickDeleteItem}/>
          </ul>
        </div>
      )}
    </li>
  );
};

export default Tree;